export default {
	data(){
		return{
			phone:null
		}
	},
	onLoad(options){
		var uid = options.id;
		if(typeof (uid) === undefined || uid === undefined){
			this.phone = '';
		}else{
			this.phone = uid;
		}
	},
	methods:{
		navToPage(){
			uni.navigateTo({
				url:"/pages/mys/register/register"
			})
		}
	}
}
